import React from 'react';
import { ctx } from '@vl/redata';
import DIV from '@vl/redata/DIV.macro';
import _ from 'lodash';
import { useRenderProps } from '@vl/hooks/useRenderProps';

import CategoryItemX from '@uz/unitz-components-web/CategoryItemX';
import FlatList from '@uz/unitz-components-web/FlatList';
import { Row, Col, Button } from 'antd';
import { Link } from '@uz/unitz-components-web/Link';
import { ResponsiveProps } from '@uz/unitz-components-web/Responsive';

const renderItem = useRenderProps(
  ({ item, index }) => {
    return (
      <Col lg={12} md={12} sm={24} className="mb-4 md:mb-0 xl:px-0">
        <DIV forceCtx>
          {ctx.debug(() => {
            ctx.set('@item', item);
            ctx.set('@index', index);
          })}
          <CategoryItemX />
        </DIV>
      </Col>
    );
  },
  ({ item, index }) => [index, _.get(item, 'id')]
);

const View8 = () => (
  <DIV className="component advisorListX 779BD3C8-CDF6-4067-A5A5-C1B320DFF758">
    <div className="wrapper app-row sm:px-4 xl:mx-0 sm:w-full py-6 lg:py-16">
      <h2 className="font-semibold text-main text-xl sm:text-2xl lg:text-4xl text-center mb-8 sm:mb-10">
        {ctx.get('sectionData.section.shortText')}
      </h2>
      <FlatList
        as={Row}
        gutter={24}
        data={ctx.get('ctfCategories', [])}
        renderItem={renderItem}
        keyExtractor={(item, index) => `${_.get(item, 'id')}_${index}`}
      />
      <Row className="justify-center mt-3 md:mt-9" gutter={24}>
        <Col xs={24} md={12} lg={12}>
          <Link to="/category">
            <ResponsiveProps xs={{ size: 'small' }} md={{ size: 'medium' }} lg={{ size: 'large' }}>
              {(resProps) => (
                <Button block {...resProps} onClick={ctx.get('form.onSubmit')}>
                  <h5 className="mb-0 text-base font-semibold text-brandb500">
                    {ctx.apply('i18n.t', 'Category.allCategory')}
                  </h5>
                </Button>
              )}
            </ResponsiveProps>
          </Link>
        </Col>
      </Row>
    </div>
  </DIV>
);

export default View8;
